<template>
  <div id="chargeBox">
    <section class="elecharge flex"
             v-for="(item,i) in currentInfo"
             :key="i">
      <div class="charge_left">
        <p>电费充值</p>
        <span>{{ item.payTime }}</span>
      </div>
      <div class="charge_right">
        <p>&yen;{{ item.amount }}</p>
        <span>{{ orderSourceMap[item.payment] }}</span>
      </div>
    </section>
  </div>
</template>
<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessElecBillByorderroom } from '@/api/api_business_hotel_and_scale';
export default {
    data() {
        return {
            currentInfo: [],
            orderSourceMap: {}
        };
    },
    created() {
        this.$store.commit('setHeader', '充值记录');
        this.getCurrentInfo();
    },
    methods: {
        getCurrentInfo() {
            GetBusinessElecBillByorderroom(this.$route.query.id).done((result) => {
                this.getPayMent();
                this.currentInfo = result.data;
            });
        },
        getPayMent() {
            GetSystemDictDataByType({ dictType: 'business_payment' }).done((res) => {
                this.orderSourceMap = {};
                res.data.forEach((item) => {
                    this.orderSourceMap[item.dictSort] = item.dictLabel;
                });
            });
        }
    }
};
</script>
<style scoped>
.flex {
    display: flex;
    justify-content: space-between;
    padding: 8px 10px;
    font-size: 0.6rem;
    border-bottom: 1px solid #f9f9f9;
    line-height: 20px;
}
#chargeBox {
    color: #c3c3c3;
    background: #fff;
    color: #c3c3c3;
    background: #fff;
    position: fixed;
    top: 60px;
    bottom: 10px;
    left: 0;
    right: 0;
    overflow: auto;
}
#chargeBox > .elecharge > .charge_left {
    text-align: left;
}
#chargeBox > .elecharge > .charge_left > p {
    color: #000;
    font-size: 0.8rem;
}
#chargeBox > .elecharge > .charge_right {
    text-align: right;
}
#chargeBox > .elecharge > .charge_right > p {
    color: #ff8b55;
    font-size: 0.8rem;
}
</style>



